@use '../../styles/var' as color;
@use '../../styles/mixin';

.nav__container {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-end;
  .main {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    margin-top: 12px;
    margin-left: 1.5rem;
    .logo {
      display: block;
      width: 32px;
      height: 32px;
      overflow: hidden;
      img {
        content: url('../../public/logo@2x.png');
        width: 100%;
        display: block;
      }
    }
    .menu {
      position: unset;
      background-color: unset;
      display: flex;
      flex-direction: column;
      align-items: center;
      width: 100%;
      margin: 39.8px 0 35.6px;
      gap: 45.6px;
    }
  }
  .signOut {
    display: block;
    margin-bottom: 16px;
    cursor: pointer;
    img {
      content: url('../../public/nav/nav_signout@2x.png');
      width: 24px;
      height: 24px;
      display: block;
    }
    .btn__name {
      display: none;
    }
  }
}

@media screen and (min-width: 870px) {
  .nav__container {
    align-items: flex-start;
    .main {
      align-items: flex-start;
      width: 178px;
      margin-left: 0;
      .logo {
        margin-left: 13px;
      }
      .menu {
        margin-left: 16.9px;
        align-items: flex-start;
      }
    }
    .signOut {
      display: flex;
      margin-left: 16px;
      align-items: center;
      gap: 16px;

      .btn__name {
        display: block;
        width: inherit;
        @include mixin.font-setting(1.125rem, 700, color.$gray-9);
      }
    }
  }
}
