@use '../styles/mixin';
@use '../styles/var' as color;

.admin__container {
  height: 100vh;
  margin: 0 auto;
  display: flex;
  gap: 1.5rem;
  overflow: hidden;

  .adminSideBar {
    width: 3.5rem;
  }

  .admin__main {
    border: 1px solid color.$assistant-gray-color;
    display: flex;
    flex-direction: column;
    width: 100%;

    .admin__title {
      margin-top: 1.5rem;
      border-bottom: 1px solid color.$assistant-gray-color;
      padding-left: 1.3075rem;

      .title {
        width: 100%;
        height: 3.1875rem;
        @include mixin.font-setting(1.5rem, 700, color.$gray-10);
      }
    }

    .admin__card {
      height: 100%;
      overflow-y: scroll;
      overflow-x: hidden;
      padding: 1rem 0;
      margin: 0 auto;
      .adminUserCards {
        display: grid;
        grid-template-columns: 15.57rem;

        grid-gap: 1rem;
      }
    }
  }
}

@media screen and (min-width: 620px) {
  .admin__container {
    width: 100%;

    .admin__main {
      .admin__card {
        padding: 1rem 0;
        margin: 0 auto;

        .adminUserCards {
          display: grid;
          grid-template-columns: repeat(2, 15.57rem);
          grid-gap: 1rem;
        }
      }
    }
  }
}

@media screen and (min-width: 870px) {
  .admin__container {
    width: 100%;

    .adminSideBar {
      width: 11.125rem;
      margin-left: 8.125rem;
    }

    .admin__main {
      .admin__card {
        .adminUserCards {
          display: grid;
          grid-template-columns: repeat(2, 15.57rem);
          // grid-template-columns: repeat(auto-fit, 15.57rem);
          // grid-template-columns: repeat(auto-fit, minmax(15.57rem, 1fr));
          // grid-auto-rows: 341px;
          grid-gap: 1rem;
        }
      }
    }
  }
}

@media screen and (min-width: 1120px) {
  .admin__container {
    .admin__main {
      .admin__card {
        .adminUserCards {
          display: grid;
          grid-template-columns: repeat(3, 15.57rem);
          grid-gap: 1rem;
        }
      }
    }
  }
}

@media screen and (min-width: 1400px) {
  .admin__container {
    .admin__main {
      .admin__card {
        margin: 0;
        padding: 1rem 0 1rem 1rem;

        .adminUserCards {
          display: grid;
          grid-template-columns: repeat(auto-fit, 15.57rem);
          grid-gap: 1rem;
        }
      }
    }
  }
}

